    },
    cleanForm() {

            this.input = '',
          this.priority = '',
          this.humanUse = false,
          this.tradeUser = false,
          this.dateStart = '',
          this.dateStart2 = '',
          this.dateEnd = '',
          this.dateEnd2 = '',
          this.workingTime = ''
      this.deadLine = ''
    },

    // 
    getActiveList() {
      getAct(this.commodityName, this.flatName).then(res => {
        this.valueRisk = res.data.data[0][0].riskvalue
